import VConsole from 'vconsole';
const vCon = new VConsole({
    log: {
        showTimestamps: true
    },
    theme: 'dark',
    onReady: () => {
        console.log('VConsole ready.');
        window.con.hideSwitch();
    }
});
let customized_plug = new VConsole.VConsolePlugin('d_front_plug', '');
customized_plug.on('renderTab', (callback) => {
    let html = '<div>' +
        '<h3></h3>' +
        '<p></p>' +
        '</div>';
    callback(html);
});
customized_plug.on('addTool', function(callback) {
    callback([{
        name: '',
        onClick: function(event) {
            console.log(event.isTrusted);
            if (window.confirm('')) {
                location.reload();
            }
        }
    }, {
        name: 'VCon',
        onClick: function(event) {
            console.log('VCon', event.isTrusted);
            alert('VConsole ');
            window.con.showSwitch();
        }
    }]);
});

vCon.addPlugin(customized_plug);
export default vCon;


