import React, {Component} from 'react';
import {Button, ButtonArea, Form, FormCell, CellHeader, CellBody, CellFooter, Label, Input, VCode} from 'react-weui';
import 'weui';
import 'react-weui/build/packages/react-weui.css';
import './index.css';
import Header from '../../Layout/Header';
import vCodeDemo from '../../logo.svg';
import TabBar from "../../Layout/Tabbar";
import SSOLoginForm from "./components/ssoLoginForm";
import { joinApi, USER_LOGIN_PATH } from "../../../Api/endpoints";

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            fullYear: '',
            casIntegrated: true
        };
    }
    componentWillMount() {
        this.setState({
            fullYear: new Date().getFullYear(),
        })
    }

    render() {
        if (this.state.casIntegrated) {
            return (
                <div className="App">
                    <SSOLoginForm loginTitle={" - "} servletUrl={joinApi(USER_LOGIN_PATH)} />
                    <TabBar />
                </div>
            );
        } else {
            return (
                <div className="App">
                    <Header appTitle=""/>
                    <p className="App-intro">
                         <code></code> 
                    </p>
                    <Form>
                        <FormCell>
                            <CellHeader>
                                <Label></Label>
                            </CellHeader>
                            <CellBody>
                                <Input type="text" placeholder="/"/>
                            </CellBody>
                        </FormCell>
                        <FormCell>
                            <CellHeader>
                                <Label></Label>
                            </CellHeader>
                            <CellBody>
                                <Input type="password" placeholder=""/>
                            </CellBody>
                        </FormCell>
                        <FormCell>
                            <CellHeader>
                                <Label></Label>
                            </CellHeader>
                            <CellBody>
                                <Input type="text" placeholder=""/>
                            </CellBody>
                            <CellFooter>
                                <VCode src={vCodeDemo}/>
                            </CellFooter>
                        </FormCell>
                    </Form>
                    <ButtonArea direction="horizontal">
                        <Button type="warn"></Button>
                        <Button type="primary"></Button>
                    </ButtonArea>
                    <p className="loginFooter">
                        APP
                    </p>
                    <p className="App-footer">
                         <code></code> 
                        <br/>
                        &copy; 2012-{this.state.fullYear} DingStudio Technology
                    </p>
                    <TabBar />
                </div>
            );
        }
    }
}

export default Login;



